<template>
  <div>
    <v-container>
      <v-row no-gutters>
        <v-col>
          <v-navigation-drawer
            left
            permanent
            fixed
            clipped
            class="hidden-sm-and-down"
          >
            <v-list class="mt-16" shaped>
              <v-list-item>
                <v-list-item-title class="title ml-7 text-decoration-underline"
                  >目录</v-list-item-title
                >
              </v-list-item>
              <v-hover v-slot="{ hover }">
                <v-list-item
                  @click="$vuetify.goTo('#date')"
                  :class="{ 'red white--text': hover }"
                >
                  <v-list-item-title class="ml-8">重要日期</v-list-item-title>
                </v-list-item>
              </v-hover>

              <v-hover v-slot="{ hover }">
                <v-list-item
                  @click="$vuetify.goTo('#notice')"
                  :class="{ 'red white--text': hover }"
                >
                  <v-list-item-title class="ml-8">注意事项</v-list-item-title>
                </v-list-item>
              </v-hover>

              <v-hover v-slot="{ hover }">
                <v-list-item
                  @click="$vuetify.goTo('#form_faq')"
                  :class="{ 'red white--text': hover }"
                >
                  <v-list-item-title class="ml-8"
                    >填写表格疑问</v-list-item-title
                  >
                </v-list-item>
              </v-hover>

              <v-hover v-slot="{ hover }">
                <v-list-item
                  @click="$vuetify.goTo('#class_faq')"
                  :class="{ 'red white--text': hover }"
                >
                  <v-list-item-title class="ml-8"
                    >日常上课疑问</v-list-item-title
                  >
                </v-list-item>
              </v-hover>

              <v-hover v-slot="{ hover }">
                <v-list-item
                  @click="$vuetify.goTo('#course_faq')"
                  :class="{ 'red white--text': hover }"
                >
                  <v-list-item-title class="ml-8">科系问题</v-list-item-title>
                </v-list-item>
              </v-hover>

              <v-hover v-slot="{ hover }">
                <v-list-item
                  @click="$vuetify.goTo('#scolarship_faq')"
                  :class="{ 'red white--text': hover }"
                >
                  <v-list-item-title class="ml-8">奖贷学金</v-list-item-title>
                </v-list-item>
              </v-hover>

              <v-hover v-slot="{ hover }">
                <v-list-item
                  @click="$vuetify.goTo('#hostel_faq')"
                  :class="{ 'red white--text': hover }"
                >
                  <v-list-item-title class="ml-8">宿舍问题</v-list-item-title>
                </v-list-item>
              </v-hover>

              <v-hover v-slot="{ hover }">
                <v-list-item
                  @click="$vuetify.goTo('#environment_faq')"
                  :class="{ 'red white--text': hover }"
                >
                  <v-list-item-title class="ml-8"
                    >校内&校外环境与生活问题</v-list-item-title
                  >
                </v-list-item>
              </v-hover>

              <v-hover v-slot="{ hover }">
                <v-list-item
                  @click="$vuetify.goTo('#degree_faq')"
                  :class="{ 'red white--text': hover }"
                >
                  <v-list-item-title class="ml-8">往后升学</v-list-item-title>
                </v-list-item>
              </v-hover>

              <v-hover v-slot="{ hover }">
                <v-list-item
                  @click="$vuetify.goTo('#upu_faq')"
                  :class="{ 'red white--text': hover }"
                >
                  <v-list-item-title class="ml-8"
                    >UPU申请问题</v-list-item-title
                  >
                </v-list-item>
              </v-hover>
            </v-list>
          </v-navigation-drawer>
        </v-col>

        <v-col cols="12" class="col-md-10 mr-md-8">
          <v-row>
            <v-col>
              <p class="text-center display-1">仍在开发中...</p>
            </v-col>
          </v-row>

          <v-row id="date">
            <v-col>
              <p class="headline text-center">
                <v-icon
                  color="red"
                  class="animate__animated animate__fadeInLeft animate__infinite"
                  >mdi-hand-pointing-right</v-icon
                >
                重要日期
                <v-icon
                  color="red"
                  class="animate__animated animate__fadeInRight animate__infinite"
                  >mdi-hand-pointing-left</v-icon
                >
              </p>
              <p class="title">UTM FOUNDATION PROGRAMME</p>
              <p class="subtitle-1">INTAKE: JULY</p>
              <v-simple-table>
                <template v-slot:default>
                  <thead>
                    <tr>
                      <th>No</th>
                      <th>Item</th>
                      <th>Local Date</th>
                      <th>International Date</th>
                    </tr>
                  </thead>
                  <tbody>
                    <tr>
                      <td>1</td>
                      <td>Online Application</td>
                      <td>9 March 2022- 10 June 2022</td>
                      <td>23 Feb 2022 - 1 June 2022</td>
                    </tr>
                    <tr>
                      <td>2</td>
                      <td>Status Application</td>
                      <td>
                        Starting one day after the official SPM result released
                      </td>
                      <td>Starting from 9 March</td>
                    </tr>
                    <tr>
                      <td>3</td>
                      <td>Virtual Open Day</td>
                      <td>TBA</td>
                      <td>TBA</td>
                    </tr>
                    <tr>
                      <td>4</td>
                      <td>New Student Registration Day</td>
                      <td>20 July 2022*</td>
                      <td>13 – 18 July 2022</td>
                    </tr>
                    <tr>
                      <td>5</td>
                      <td>Semester II Lecture Begin</td>
                      <td>
                        24 July 2022* (Johor Bahru Campus) <br />
                        25 July 2022* (Kuala Lumpur Campus)
                      </td>
                      <td>
                        24 July 2022* (Johor Bahru Campus) <br />
                        25 July 2022* (Kuala Lumpur Campus)
                      </td>
                    </tr>
                  </tbody>
                </template>
              </v-simple-table>
            </v-col>
          </v-row>

          <v-row class="mt-16" id="notice">
            <v-col>
              <p class="headline text-center">
                <v-icon
                  color="red"
                  class="animate__animated animate__fadeInLeft animate__infinite"
                  >mdi-hand-pointing-right</v-icon
                >
                注意事项
                <v-icon
                  color="red"
                  class="animate__animated animate__fadeInRight animate__infinite"
                  >mdi-hand-pointing-left</v-icon
                >
              </p>

              <v-stepper v-model="e1" non-linear alt-labels>
                <v-stepper-header>
                  <v-stepper-step
                    step="1"
                    editable
                    color="red"
                    @click="$vuetify.goTo('#notice')"
                  >
                    第一项
                  </v-stepper-step>

                  <v-divider></v-divider>

                  <v-stepper-step
                    step="2"
                    editable
                    color="red"
                    @click="$vuetify.goTo('#notice')"
                  >
                    第二项
                  </v-stepper-step>

                  <v-divider></v-divider>

                  <v-stepper-step
                    step="3"
                    editable
                    color="red"
                    @click="$vuetify.goTo('#notice')"
                  >
                    第三项
                  </v-stepper-step>

                  <v-divider></v-divider>

                  <v-stepper-step
                    step="4"
                    editable
                    color="red"
                    @click="$vuetify.goTo('#notice')"
                  >
                    第四项
                  </v-stepper-step>
                </v-stepper-header>

                <v-stepper-items>
                  <v-stepper-content step="1">
                    <v-card class="mb-12" height="200px">
                      <v-card-title class="d-flex justify-center fill-height"
                        >拿到Offer后，<span class="font-weight-bold red--text"
                          >无需按Accepted</span
                        >
                        !</v-card-title
                      >
                    </v-card>
                    <v-btn
                      color="red white--text"
                      @click="
                        e1 = 2;
                        $vuetify.goTo('#notice');
                      "
                      >下一项</v-btn
                    >
                  </v-stepper-content>

                  <v-stepper-content step="2">
                    <v-card class="mb-12">
                      <v-card-title class="d-flex justify-center fill-height"
                        >接着点击View history, 找到Offer Confirmed
                        section里面的link缴付RM300的BMK。（如下）</v-card-title
                      >

                      <v-img
                        src="../assets/foundation_bmk1.jpg"
                        max-width="716"
                        class="elevation-12 mx-auto"
                      ></v-img>

                      <v-img
                        src="../assets/foundation_bmk2.png"
                        max-width="657"
                        class="elevation-12 mx-auto mt-8"
                      ></v-img>
                    </v-card>
                    <v-btn
                      color="red white--text"
                      @click="
                        e1 = 3;
                        $vuetify.goTo('#notice');
                      "
                      >下一项</v-btn
                    >
                    <v-btn
                      @click="
                        e1 = 1;
                        $vuetify.goTo('#notice');
                      "
                      text
                      color="red"
                      >返回</v-btn
                    >
                  </v-stepper-content>

                  <v-stepper-content step="3">
                    <v-card class="mb-12">
                      <v-card-title class="d-flex justify-center fill-height"
                        >缴付BMK并且把付款收据发给UTM Space财务处，在你的Sistem
                        Ambilan Mahasiswa（SAM）的status会更新成Accepted.
                        (如下）</v-card-title
                      >
                      <v-img
                        src="../assets/foundation_status.png"
                        max-width="635"
                        class="elevation-12 mx-auto"
                      ></v-img>
                    </v-card>
                    <v-btn
                      color="red white--text"
                      @click="
                        e1 = 4;
                        $vuetify.goTo('#notice');
                      "
                      >下一项</v-btn
                    >
                    <v-btn
                      @click="
                        e1 = 2;
                        $vuetify.goTo('#notice');
                      "
                      text
                      color="red"
                      >返回</v-btn
                    >
                  </v-stepper-content>

                  <v-stepper-content step="4">
                    <v-card class="mb-12">
                      <v-card-title class="d-flex justify-center fill-height"
                        >接下来下载右手边的文件，并且填写
                        !（如下）</v-card-title
                      >

                      <v-img
                        src="../assets/foundation_doc.jpg"
                        max-width="1125 mx-auto"
                      ></v-img>
                    </v-card>
                    <v-btn
                      color="red white--text"
                      @click="
                        e1 = 1;
                        $vuetify.goTo('#notice');
                      "
                      >重来</v-btn
                    >
                    <v-btn
                      @click="
                        e1 = 3;
                        $vuetify.goTo('#notice');
                      "
                      text
                      color="red"
                      >返回</v-btn
                    >
                  </v-stepper-content>
                </v-stepper-items>
              </v-stepper>
            </v-col>
          </v-row>

          <v-row class="mt-16" id="form_faq">
            <v-col>
              <p class="headline text-center">
                <v-icon
                  color="red"
                  class="animate__animated animate__fadeInLeft animate__infinite"
                  >mdi-hand-pointing-right</v-icon
                >
                填写表格疑问
                <v-icon
                  color="red"
                  class="animate__animated animate__fadeInRight animate__infinite"
                  >mdi-hand-pointing-left</v-icon
                >
              </p>

              <v-expansion-panels>
                <v-expansion-panel>
                  <v-expansion-panel-header>
                    1. No. Pendaftaran / Program 要填什么？
                  </v-expansion-panel-header>
                  <v-expansion-panel-content>
                    No. Pendaftaran是Surat tawaran上面的No
                    rujukan，Program呢就是你拿到的科系，Kod Program
                    可以查询Panduan Borang P5。 <br />
                    <span class="red--text">***Foundation同学的kod是FSPA</span>
                  </v-expansion-panel-content>
                </v-expansion-panel>

                <v-expansion-panel>
                  <v-expansion-panel-header>
                    2. Tinggi / Kumpulan darah 如果我不知道的话可以空着吗？
                  </v-expansion-panel-header>
                  <v-expansion-panel-content>
                    血型不懂可以先空着。
                  </v-expansion-panel-content>
                </v-expansion-panel>

                <v-expansion-panel>
                  <v-expansion-panel-header>
                    3. Agregat / Pangkat 要填什么？
                  </v-expansion-panel-header>
                  <v-expansion-panel-content>
                    Agregat和Pangkat不需要填哦。
                  </v-expansion-panel-content>
                </v-expansion-panel>

                <v-expansion-panel>
                  <v-expansion-panel-header>
                    4. Penjamin可以放父母/亲戚（跟Saudara pertama &
                    kedua一样）吗？
                  </v-expansion-panel-header>
                  <v-expansion-panel-content>
                    <span class="red--text">**** 有更新，不好意思</span>
                    <br />
                    Penjamin
                    可以放父母也可以放符合条件的亲戚，条件为月薪需要超过RM500（但需注意Saudara
                    pertama & kedua需要填写父母以外可以第一时间联络到的人）。
                  </v-expansion-panel-content>
                </v-expansion-panel>

                <v-expansion-panel>
                  <v-expansion-panel-header>
                    5. 如果RM10的setem hasil完了，我可以用两张RM5的setem
                    hasil代替吗？
                  </v-expansion-panel-header>
                  <v-expansion-panel-content>
                    可以，但是要注意黏上去时候2张setem hasil没有重叠到。
                  </v-expansion-panel-content>
                </v-expansion-panel>

                <v-expansion-panel>
                  <v-expansion-panel-header>
                    6. 哪里可以matikan setem？UTC可以吗？
                  </v-expansion-panel-header>
                  <v-expansion-panel-content>
                    UTC已经不能matikan setem
                    了，必须去LHDN。此外，Pahang的Pejabat Tanah也可以matikan
                    setem。
                  </v-expansion-panel-content>
                </v-expansion-panel>

                <v-expansion-panel>
                  <v-expansion-panel-header>
                    7. 如果我本人没有去而是请朋友帮我matikan setem可以吗？
                  </v-expansion-panel-header>
                  <v-expansion-panel-content>
                    不可以哦，因为matikan
                    setem时候需要填写个人资料，建议是亲自去。
                  </v-expansion-panel-content>
                </v-expansion-panel>

                <v-expansion-panel>
                  <v-expansion-panel-header>
                    8. Setem hasil要去哪里买？
                  </v-expansion-panel-header>
                  <v-expansion-panel-content>
                    可以到Pejabat Pos购买或是直接在Lembaga Hasil Dalam Negeri
                    (LHDN) 买了然后matikan
                    setem。（注意：申请PTPTN时候也需要Setem
                    hasil，但是需要官员到校亲自matikan
                    setem，建议事先买了保存收好）
                    <ul>
                      <li>
                        即日起LHDN已经废除以下的样子的Setem Hasil，以Sijil
                        Setem来代替了，仍然可以使用于Borang 3a跟3b哦
                      </li>
                      <li>PTPTN的setem hasil建议在Pos Malaysia购买。</li>
                    </ul>
                    <br />
                    <v-row>
                      <v-col md="2">
                        <v-row>
                          <v-img
                            src="../assets/setem1.png"
                            max-width="132"
                            class="ml-2 ml-md-7"
                          ></v-img>
                        </v-row>
                        <v-row> Pos Malaysia的Setem Hasil </v-row>
                      </v-col>

                      <v-col md="2">
                        <v-row>
                          <v-img
                            src="../assets/setem2.png"
                            max-width="112"
                            class="ml-3"
                          ></v-img>
                        </v-row>
                        <v-row> LHDN的Setem Hasil </v-row>
                      </v-col>
                    </v-row>
                  </v-expansion-panel-content>
                </v-expansion-panel>

                <v-expansion-panel>
                  <v-expansion-panel-header>
                    9. 我需要准备多少张RM10的setem hasil呢？
                  </v-expansion-panel-header>
                  <v-expansion-panel-content>
                    总共是4张（若没有申请PTPTN，只需2张） <br />
                    a. 2张贴在Borang 3a & 3b，需要自己到LHDN去matikan setem
                    <br />
                    b. 2张准备给PTPTN，不需要matikan setem否则就作废了
                  </v-expansion-panel-content>
                </v-expansion-panel>

                <v-expansion-panel>
                  <v-expansion-panel-header>
                    10. 我的父母退休了/没有工作，请问需要填写Borang Pengesahan
                    Pendapatan吗？
                  </v-expansion-panel-header>
                  <v-expansion-panel-content>
                    需要填写Borang Pengesahan
                    Pendapatan，薪水则填写RM0。若是退休的公务员，每个月都会有一份报告单，可以用来作为证明。而如果是家里母亲为家庭主妇的话，则不需要填这个borang。
                  </v-expansion-panel-content>
                </v-expansion-panel>

                <v-expansion-panel>
                  <v-expansion-panel-header>
                    11. 请问我可以找谁sahkan我的borang和文件？
                  </v-expansion-panel-header>
                  <v-expansion-panel-content>
                    最方便的是寻找（政府）学校的校长/副校长，如果他们都不在可以询问任何主任是否适合并且愿意帮忙。（若学校行政因Covid-19受影响而关闭的话，建议寻找您区域的州/国议员，帮你sah）
                    <br />
                    <ol type="a" start="3">
                      <li>
                        sah文件副本可以找Pengetua/ Guru Besar, Pegawai Kerajaan
                        Kumpulan 1, Wakil Rakyat, Jaksa Pendamai (JP), Ketua
                        Jajahan, Ketua Bahagian atau Ketua Kampung.
                      </li>
                      <li>
                        签Penjamin的Borang可以找Wakil Rakyat, Jaksa Pendamai,
                        Pegawai Kerajaan Bahagian 1, Pengetua/Guru Besar, Ketua
                        Jajahan, Ketua Bahagian dan Ketua Kampung.
                      </li>
                      <li>
                        而Borang Pengesahan Pendapatan有注明需要Pengetua, Guru
                        Besar Sekolah (Sekolah Kerajaan sahaja), Penghulu,
                        Penggawa, Tuai Rumah, Ketua Kaum atau Mukim Bertauliah,
                        Pegawai Kerajaan Kumpulan Pengurusan dan Profesional
                        (dahulunya Kumpulan A), Pengamal Undang-Undang,
                        Pesuruhjaya Sumpah, Jaksa Pendamai atau Ahli Majlis
                        Mesyuarat Negeri (Ahli EXCO).
                      </li>
                    </ol>
                  </v-expansion-panel-content>
                </v-expansion-panel>

                <v-expansion-panel>
                  <v-expansion-panel-header>
                    12. Borang P5 里面的第26项 Alamat Semasa Pengajian要填吗？
                  </v-expansion-panel-header>
                  <v-expansion-panel-content>
                    不需要哦，除非大学期间不住在校内宿舍。
                  </v-expansion-panel-content>
                </v-expansion-panel>

                <v-expansion-panel>
                  <v-expansion-panel-header>
                    13.
                    我印出来了才发现我有填错东西，我需要重新印过还是可以用correction
                    tape修改吗？
                  </v-expansion-panel-header>
                  <v-expansion-panel-content>
                    如果发现表格有需要修改的地方，建议使用红笔做出更改就好。
                  </v-expansion-panel-content>
                </v-expansion-panel>

                <v-expansion-panel>
                  <v-expansion-panel-header>
                    14. 请问我需要用70gsm还是80gsm的纸复印我的文件呢？
                  </v-expansion-panel-header>
                  <v-expansion-panel-content>
                    没有特别要求需要用哪一种纸哦，但是建议使用80gsm的纸打印重要文件，以防呈交时文件受损。
                  </v-expansion-panel-content>
                </v-expansion-panel>

                <v-expansion-panel>
                  <v-expansion-panel-header>
                    15. 有什么borang是需要印两份出来的吗？
                  </v-expansion-panel-header>
                  <v-expansion-panel-content>
                    建议根据校方给的Set Tawaran Kemasukan来打印和复印。
                    <v-img
                      src="../assets/foundation_brg.jpg"
                      max-width="728"
                    ></v-img>
                  </v-expansion-panel-content>
                </v-expansion-panel>

                <v-expansion-panel>
                  <v-expansion-panel-header>
                    16. 有什么文件是需要sahkan的？
                  </v-expansion-panel-header>
                  <v-expansion-panel-content>
                    <ol type="a">
                      <li>Salinan Surat Tawaran (2 Salinan)</li>
                      <li>Salinan Sijil SPM atau setaraf (2 Salinan)</li>
                      <li>Salinan Kad Pengenalan (2 Salinan)</li>
                      <li>
                        Salinan Surat Beranak Pemohon, Ibu bapa, Penjaga (2
                        Salinan)
                      </li>
                      <li>Salinan Sijil Berhenti Sekolah (2 Salinan)</li>
                    </ol>
                  </v-expansion-panel-content>
                </v-expansion-panel>

                <v-expansion-panel>
                  <v-expansion-panel-header>
                    17. Borang 要用portrait还是landscape来打印？
                  </v-expansion-panel-header>
                  <v-expansion-panel-content>
                    使用portrait来打印哦。
                  </v-expansion-panel-content>
                </v-expansion-panel>

                <v-expansion-panel>
                  <v-expansion-panel-header>
                    18. 请问薪水单是有指定要最新的吗？
                  </v-expansion-panel-header>
                  <v-expansion-panel-content>
                    是没有注明需要最新的薪水单，但是建议使用最新的哦。
                  </v-expansion-panel-content>
                </v-expansion-panel>

                <v-expansion-panel>
                  <v-expansion-panel-header>
                    19. 照片需要多少张呢?什么size的？背景要蓝底还是白底？
                  </v-expansion-panel-header>
                  <v-expansion-panel-content>
                    一份Borang
                    P5需要一张。为了避免日后申请其他事项需要用到，可以准备多几张预备着。根据Borang
                    P5的要求是ic size的，只可以使用白底。
                  </v-expansion-panel-content>
                </v-expansion-panel>

                <v-expansion-panel>
                  <v-expansion-panel-header>
                    20. Penjamin需要薪水单吗？（注意:
                    Penjamin需要至少500或以上个的收入才能当penjamin）
                  </v-expansion-panel-header>
                  <v-expansion-panel-content>
                    Penjamin不需要薪水单哦，他们是作为你在utm读书期间的担保人，需要担保你在读书期间有办法偿还学费以及其他费用。校方若在处理费用方面出了问题将会联系这两位担保人。
                  </v-expansion-panel-content>
                </v-expansion-panel>

                <v-expansion-panel>
                  <v-expansion-panel-header>
                    21. 请问下Borang 3a & 3b
                    如果penjamin的房子贷款还没付完，就不算自己的所以不需要写进去是吗？
                  </v-expansion-panel-header>
                  <v-expansion-panel-content>
                    其实是可以写进去的，因为很多人的房子都是在贷款。虽然说是在贷款，但是名义上房子已经属于自己的了，只是和银行借钱。
                  </v-expansion-panel-content>
                </v-expansion-panel>

                <v-expansion-panel>
                  <v-expansion-panel-header>
                    22. 如果工作没有kwsp的话，gaji bersih和jumlah
                    pendapatan可以写一样的吗？
                  </v-expansion-panel-header>
                  <v-expansion-panel-content>
                    可以。
                  </v-expansion-panel-content>
                </v-expansion-panel>

                <v-expansion-panel>
                  <v-expansion-panel-header>
                    23. 那个照片我可以自己拍吗？
                  </v-expansion-panel-header>
                  <v-expansion-panel-content>
                    建议到外面的照相馆拍，记得要穿formal或者有领的整齐衣服。
                  </v-expansion-panel-content>
                </v-expansion-panel>

                <v-expansion-panel>
                  <v-expansion-panel-header>
                    24. Nama majikan 是写公司名字还是老板的名字？
                  </v-expansion-panel-header>
                  <v-expansion-panel-content>
                    <ol type="a">
                      <li>Alamat Pejabat 写工作的地址</li>
                      <li>Nama Majikan 写雇主/公司的名字</li>
                      <li>Alamat Majikan 写公司注册的地址</li>
                    </ol>
                  </v-expansion-panel-content>
                </v-expansion-panel>

                <v-expansion-panel>
                  <v-expansion-panel-header>
                    25. Borang
                    P5里面需要我填写银行名字以及户口号码，请问一下UTM的Panel
                    Bank是什么银行？
                  </v-expansion-panel-header>
                  <v-expansion-panel-content>
                    <v-simple-table>
                      <thead>
                        <tr>
                          <th>Campus</th>
                          <th>Panel Bank Name</th>
                        </tr>
                      </thead>
                      <tbody>
                        <tr>
                          <td>UTM Skudai</td>
                          <td>CIMB Bank</td>
                        </tr>
                        <tr>
                          <td>UTM Kuala Lumpur</td>
                          <td>Bank Islam</td>
                        </tr>
                      </tbody>
                    </v-simple-table>
                    <span class="font-weight-bold"
                      >建议在学校的panel
                      bank开户！！若有任何退款等等，学校会优先处理相同银行的户口，非panel
                      bank的户口会比较慢受理。</span
                    >
                    <br />
                    <span class="font-weight-bold red--text"
                      >PTPTN的银行户口是强制性的！！！必须在指定银行开户，不可以使用其他银行的户口来申请PTPTN！！！</span
                    >
                  </v-expansion-panel-content>
                </v-expansion-panel>

                <v-expansion-panel>
                  <v-expansion-panel-header>
                    26.
                    我现在没有任何个人名字银行户口，可是我有与父母的联名户口，请问可以用吗？
                  </v-expansion-panel-header>
                  <v-expansion-panel-content>
                    如果是联名户口，建议把他分出来成个人户口或者开设一个新的个人户口，因为当UTM需要退款的时候，必须核实到时学生的名字方可继续处理，<span
                      class="font-weight-bold"
                      >若没有将会有所耽搁或者照成退款失败。<span class="yellow"
                        >（如果你还没有过生日，一样可以开户！只是银行账户会限定交易额度，这个额度可以在生日后到银行更新。）</span
                      ></span
                    >
                  </v-expansion-panel-content>
                </v-expansion-panel>

                <v-expansion-panel>
                  <v-expansion-panel-header>
                    27. 我还没开户，需要带什么到银行开户？
                  </v-expansion-panel-header>
                  <v-expansion-panel-content>
                    需要开户的东西如下：（有分18岁以上或者18岁以下，年龄以生日日期为准）
                    <v-simple-table>
                      <thead>
                        <tr>
                          <th>18岁以上</th>
                          <th>18岁以下</th>
                        </tr>
                      </thead>
                      <tbody>
                        <tr>
                          <td>RM100或以上</td>
                          <td>RM100或以上</td>
                        </tr>
                        <tr>
                          <td>Offer Letter/Surat Kebenaran</td>
                          <td>Offer Letter/Surat Kebenaran</td>
                        </tr>
                        <tr>
                          <td>-</td>
                          <td>报生纸（Sijil Kelahiran/Birth Certificate）</td>
                        </tr>
                        <tr>
                          <td>-</td>
                          <td>任何一位父母（需要帮忙签名）</td>
                        </tr>
                      </tbody>
                    </v-simple-table>
                  </v-expansion-panel-content>
                </v-expansion-panel>

                <v-expansion-panel>
                  <v-expansion-panel-header>
                    28. 有没有任何的Borang kemasukan的sample ?
                  </v-expansion-panel-header>
                  <v-expansion-panel-content>
                    有，可以参考这个链接：<a
                      href="https://drive.google.com/drive/folders/18vRsYVu0sjYLAtdbQeIuMl0Qo_Rqg7wh?usp=sharing"
                      target="_blank"
                      class="red--text"
                      >BORANG KEMASUKAN KE UTM - SAMPLE</a
                    >
                    <br />
                    <span class="font-weight-bold"
                      >（如果Folder无法打开，需要授权的话，请您联系学长学姐处理，谢谢）</span
                    >
                  </v-expansion-panel-content>
                </v-expansion-panel>
              </v-expansion-panels>
            </v-col>
          </v-row>

          <v-row class="mt-16" id="class_faq">
            <v-col>
              <p class="headline text-center">
                <v-icon
                  color="red"
                  class="animate__animated animate__fadeInLeft animate__infinite"
                  >mdi-hand-pointing-right</v-icon
                >
                日常上课疑问
                <v-icon
                  color="red"
                  class="animate__animated animate__fadeInRight animate__infinite"
                  >mdi-hand-pointing-left</v-icon
                >
              </p>

              <v-expansion-panels>
                <v-expansion-panel>
                  <v-expansion-panel-header>
                    1. idp生是和mainstream生一起上课的还是分开？
                  </v-expansion-panel-header>
                  <v-expansion-panel-content>
                    两者是一起上课的哦。
                  </v-expansion-panel-content>
                </v-expansion-panel>

                <v-expansion-panel>
                  <v-expansion-panel-header>
                    2. 上课和考试时候是用什么语言啊？
                  </v-expansion-panel-header>
                  <v-expansion-panel-content>
                    除了部分大专必修科（University General
                    Course），其他科目考试时候一定是用英文。上课的话是根据
                    lecturer，教材会使用英文，但是同时也会用国文作为辅助。
                  </v-expansion-panel-content>
                </v-expansion-panel>

                <v-expansion-panel>
                  <v-expansion-panel-header>
                    3. foundation的老师跟degree的一样吗？
                  </v-expansion-panel-header>
                  <v-expansion-panel-content>
                    foundation 和
                    degree的师资是有些许不一样的。但是别担心因为utm
                    foundation的师资都是有拿到专业文凭并且可以成为合格的教授的，他们也都是utm自己的教授哦。
                  </v-expansion-panel-content>
                </v-expansion-panel>

                <v-expansion-panel>
                  <v-expansion-panel-header>
                    上课的衣服有什么要求吗？
                  </v-expansion-panel-header>
                  <v-expansion-panel-content>
                    最基本是有领的短袖上衣，长裤和包鞋。另外，校方有规定过星期一是formal
                    wear。 <br />
                    <a
                      href="https://www.utm.my/studentaffairs/panduan/"
                      target="_blank"
                      class="red--text"
                      >https://www.utm.my/studentaffairs/panduan/</a
                    >
                    <br />
                    **这里附上校方的etika pemakaian
                  </v-expansion-panel-content>
                </v-expansion-panel>

                <v-expansion-panel>
                  <v-expansion-panel-header>
                    5.
                    上课有需要准备些什么standard的簿子或是textpad吗？还是多数用电脑？
                  </v-expansion-panel-header>
                  <v-expansion-panel-content>
                    可以自备簿子或者白纸方便记录资料，教学的讲义和内容都需要根据lecturer，有的会给softcopy让我们自行打印（例如PowerPoint）有的则是要求需要用指定的书本。
                  </v-expansion-panel-content>
                </v-expansion-panel>

                <v-expansion-panel>
                  <v-expansion-panel-header>
                    6. Johor的周末是在星期五和六吗？
                  </v-expansion-panel-header>
                  <v-expansion-panel-content>
                    是的，所以星期五和六是没有上课的，除非教授要求。
                  </v-expansion-panel-content>
                </v-expansion-panel>

                <v-expansion-panel>
                  <v-expansion-panel-header>
                    7. 请问我现在读的是第一个学期 （Semester
                    I），为什么在学校的官方文件里面是写我们现在在读着第二个学期（Semester
                    II）？
                  </v-expansion-panel-header>
                  <v-expansion-panel-content>
                    2020年开始的Foundation课程，学校已经把原本的short
                    sem也就是sem
                    1调整到了最后一个sem，因为当时候收到Covid-19的影响，也把原定在4月开课的课程延迟到7月，学校以比较方便整理课程等等东西，所以就做出了以下的调整：
                    <br />
                    Short Sem: 10 weeks ; Long Sem: 21 weeks (Include mid &
                    final sem break)
                    <v-simple-table>
                      <thead>
                        <tr>
                          <th>学期</th>
                          <th>2020年之前</th>
                          <th>2020年之后</th>
                        </tr>
                      </thead>
                      <tbody>
                        <tr>
                          <td>1st Sem</td>
                          <td>Sem I (Short Sem)</td>
                          <td>Sem II (Long Sem)</td>
                        </tr>
                        <tr>
                          <td>2nd Sem</td>
                          <td>Sem II (Long Sem)</td>
                          <td>Sem III (Long Sem)</td>
                        </tr>
                        <tr>
                          <td>3rd Sem</td>
                          <td>Sem III (Short Sem)</td>
                          <td>Sem I (Short Sem)</td>
                        </tr>
                      </tbody>
                    </v-simple-table>
                  </v-expansion-panel-content>
                </v-expansion-panel>
              </v-expansion-panels>
            </v-col>
          </v-row>

          <v-row class="mt-16" id="course_faq">
            <v-col>
              <p class="headline text-center">
                <v-icon
                  color="red"
                  class="animate__animated animate__fadeInLeft animate__infinite"
                  >mdi-hand-pointing-right</v-icon
                >
                科系问题
                <v-icon
                  color="red"
                  class="animate__animated animate__fadeInRight animate__infinite"
                  >mdi-hand-pointing-left</v-icon
                >
              </p>

              <v-expansion-panels>
                <v-expansion-panel>
                  <v-expansion-panel-header>
                    1. Social Science跟Life Science还有Physical
                    Science有什么分别 ？
                  </v-expansion-panel-header>
                  <v-expansion-panel-content>
                    这3个课程在第一个sem所学的东西都是一样的，在第二个sem开始的时候才会有点分别，Life
                    Science呢就是比较倾向于Biology，原本的Physics
                    II的课程将会改成Biology I；然而Social
                    Science的话就是倾向于商务的课程，会在第二跟第三学期的时候专注于商务课程（可以参考下面的表来了解更加详细的资料）
                    <a
                      href="https://drive.google.com/file/d/1Nut5rfA3nusHGolAjWBZBNYDLDVIfx9e/view?usp=sharing"
                      target="_blank"
                      class="red--text"
                      >【清楚的版本，按这边】</a
                    >
                    <v-img
                      src="../assets/foundation_timetable.jpg"
                      width="1600"
                    ></v-img>
                  </v-expansion-panel-content>
                </v-expansion-panel>

                <v-expansion-panel>
                  <v-expansion-panel-header>
                    2. 我在系统申请到Life Science可是我想换去Physical
                    Science，我要怎么办？
                  </v-expansion-panel-header>
                  <v-expansion-panel-content>
                    你可以联系UTM
                    Space的招生处通过email或者拨电，告诉他们你要更换科系即可。
                  </v-expansion-panel-content>
                </v-expansion-panel>

                <v-expansion-panel>
                  <v-expansion-panel-header>
                    3. 如何申请foundation 的social science？
                  </v-expansion-panel-header>
                  <v-expansion-panel-content>
                    需要在开学之前安排好科系哦，以方便后期不会受到影响。
                  </v-expansion-panel-content>
                </v-expansion-panel>

                <v-expansion-panel>
                  <v-expansion-panel-header>
                    4. 我看到了UTM KL跟UTM JB都有Offer Foundation in Physical
                    Science，两边campus都有什么差别？
                  </v-expansion-panel-header>
                  <v-expansion-panel-content>
                    其实毫无差别，只是UTM
                    KL的foundation班可以多读日语，但是日语班是自愿性的哦。
                  </v-expansion-panel-content>
                </v-expansion-panel>

                <v-expansion-panel>
                  <v-expansion-panel-header>
                    5. 如果我对UTM KL的Foundation比较有兴趣，我可以换吗？
                  </v-expansion-panel-header>
                  <v-expansion-panel-content>
                    可以的，需要拨电给UTM Space
                    Admission来更改你的校区哦，并且你的学号也会有所更改。但是，转换学校只可以在开学前弄，开学后就无法转换校区了。
                  </v-expansion-panel-content>
                </v-expansion-panel>

                <v-expansion-panel>
                  <v-expansion-panel-header>
                    6. 我读了life science，以后可以读什么？
                  </v-expansion-panel-header>
                  <v-expansion-panel-content>
                    life science 读的内容比较偏向于生物。读完foundation in life
                    science 后呢同学可继续申请相关的科系。例如，life
                    science是理科+生物的组合，那么日后同学可申请更多关于生物方面的科系，或是部分的工程科系例如bio-medical
                    engineering也可以哦。
                  </v-expansion-panel-content>
                </v-expansion-panel>

                <v-expansion-panel>
                  <v-expansion-panel-header>
                    7. UTM Foundation 会难读吗？
                  </v-expansion-panel-header>
                  <v-expansion-panel-content>
                    这个问题因人而异，因为Physics跟Chemistry大部分都跟SPM相似滴。然后Calculus也是跟Add
                    Maths差不多，只是比较深入一点。比较有挑战的是Computing，也就是编程或者Programming。除此之外，我们也会学关于英文，普通数学，电脑课程还有企业学哦。
                  </v-expansion-panel-content>
                </v-expansion-panel>

                <v-expansion-panel>
                  <v-expansion-panel-header>
                    8.
                    如果我Foundation读到一半了，我拿到Matriculation/PISMP/UPU的offer，我可以退学吗？学费会不会退回？
                  </v-expansion-panel-header>
                  <v-expansion-panel-content>
                    你当然可以申请退学，学费的话需要看你读了多久方可决定是否会退回，退回的百分比如下：
                    <br />
                    <p class="font-weight-bold text-center">
                      期限与会被征收的学费百分比
                    </p>
                    <v-simple-table>
                      <thead>
                        <tr>
                          <th>期限</th>
                          <th>注册费</th>
                          <th>学费</th>
                          <th>宿舍费</th>
                        </tr>
                      </thead>
                      <tbody>
                        <tr>
                          <td>第一个星期到第四个星期之间</td>
                          <td>100 %</td>
                          <td>50 %</td>
                          <td>100 %</td>
                        </tr>
                        <tr>
                          <td>第五个星期之后</td>
                          <td>100 %</td>
                          <td>100 %</td>
                          <td>100 %</td>
                        </tr>
                      </tbody>
                    </v-simple-table>
                    <ul>
                      <li>欠缺的学费必须从退学申请批准信发出的30天内缴清。</li>
                      <li>
                        若有多余的学费将会在30天内退还当调整工作已经完成。
                      </li>
                      <li>
                        以上皆为翻译版，一切以原文版为准。 （原文：
                        <a
                          class="red--text"
                          href="https://studentppi.utmspace.edu.my/wp-content/uploads/2020/09/Buku-Panduan-Kewangan-bagi-Pelajar-Tempatan-Program-Asasi-UTM_2020.pdf"
                          target="_blank"
                          >Buku Panduan Kewangan Program Asasi UTM</a
                        >）
                      </li>
                    </ul>
                  </v-expansion-panel-content>
                </v-expansion-panel>

                <v-expansion-panel>
                  <v-expansion-panel-header>
                    9. UTM foundation很像私人的，是有受MQA认证的吗？
                  </v-expansion-panel-header>
                  <v-expansion-panel-content>
                    有的，UTM的foundation是UTM
                    Space自己开办的课程并且是有受到MQA的认证的，这点同学不必担心。
                  </v-expansion-panel-content>
                </v-expansion-panel>

                <v-expansion-panel>
                  <v-expansion-panel-header>
                    10. UTM Foundation的上课时间是怎样子的？
                  </v-expansion-panel-header>
                  <v-expansion-panel-content>
                    JB Campus的上课时间是从星期日到星期四；KL
                    Campus的话就是星期一到星期五。每天早上8点到下午5点都会有课，然而休息时间为下午1点到2点。
                    <v-simple-table>
                      <thead>
                        <tr>
                          <th>Credit Hour</th>
                          <th>Short Sem (9 weeks)</th>
                          <th>Long Sem (18 weeks)</th>
                        </tr>
                      </thead>
                      <tbody>
                        <tr>
                          <td>2</td>
                          <td>6 Hours/Week*</td>
                          <td>3 Hours/week*</td>
                        </tr>
                        <tr>
                          <td>4</td>
                          <td>10 Hours/Week</td>
                          <td>5 Hours/week</td>
                        </tr>
                      </tbody>
                    </v-simple-table>
                    <span class="font-weight-bold"
                      >*不包括practical课，Practical课一律3 Hours per Week per
                      Subject。</span
                    >
                  </v-expansion-panel-content>
                </v-expansion-panel>
              </v-expansion-panels>
            </v-col>
          </v-row>

          <v-row class="mt-16" id="scolarship_faq">
            <v-col>
              <p class="headline text-center">
                <v-icon
                  color="red"
                  class="animate__animated animate__fadeInLeft animate__infinite"
                  >mdi-hand-pointing-right</v-icon
                >
                奖贷学金
                <v-icon
                  color="red"
                  class="animate__animated animate__fadeInRight animate__infinite"
                  >mdi-hand-pointing-left</v-icon
                >
              </p>

              <v-expansion-panels>
                <v-expansion-panel>
                  <v-expansion-panel-header>
                    1. Utmspace foundation有什么奖学金可以申请吗？
                  </v-expansion-panel-header>
                  <v-expansion-panel-content>
                    由于UTM的Foundation课程是UTMSpace开办的所以政府不会给津贴，但是可以上网寻找其他奖贷学金。同时要记得先查看T&C是否符合。
                  </v-expansion-panel-content>
                </v-expansion-panel>

                <v-expansion-panel>
                  <v-expansion-panel-header>
                    2. Foundation可以申请ptptn吗？
                  </v-expansion-panel-header>
                  <v-expansion-panel-content>
                    UTM的Foundation课程由于课程时间少于1年，而PTPTN的要求是所剩的课程时限不少于1年，所以即使UTM
                    foundation 受MQA 认证也不可以申请PTPTN 的。 <br />
                    更多详细的借贷金额也可以到PTPTN的网站参考： <br />
                    <a
                      href="https://www.ptptn.gov.my/permohonan-pinjaman-side"
                      class="red--text"
                      target="_blank"
                      >https://www.ptptn.gov.my/permohonan-pinjaman-side</a
                    >
                  </v-expansion-panel-content>
                </v-expansion-panel>

                <v-expansion-panel>
                  <v-expansion-panel-header>
                    3. SSPN-i是什么来的？
                  </v-expansion-panel-header>
                  <v-expansion-panel-content>
                    SSPN-i是国民教育储蓄基金，简单来说就是一个储蓄户口。申请ptptn的同学都需要先开一个sspn-i的户口，因为这个户口号码是申请PTPTN的条件之一。
                    <br />
                    开户链接： <br />
                    <a
                      href="https://www.ptptn.gov.my/esmas-open-acc-web/#/openAccount"
                      class="red--text"
                      target="_blank"
                      >https://www.ptptn.gov.my/esmas-open-acc-web/#/openAccount</a
                    >
                  </v-expansion-panel-content>
                </v-expansion-panel>
              </v-expansion-panels>
            </v-col>
          </v-row>

          <v-row class="mt-16" id="hostel_faq">
            <v-col>
              <p class="headline text-center">
                <v-icon
                  color="red"
                  class="animate__animated animate__fadeInLeft animate__infinite"
                  >mdi-hand-pointing-right</v-icon
                >
                宿舍问题
                <v-icon
                  color="red"
                  class="animate__animated animate__fadeInRight animate__infinite"
                  >mdi-hand-pointing-left</v-icon
                >
              </p>

              <v-expansion-panels>
                <v-expansion-panel>
                  <v-expansion-panel-header>
                    1. Utm有巴士从宿舍去学校吗？
                  </v-expansion-panel-header>
                  <v-expansion-panel-content>
                    UTMJB和UTMKL
                    都有提供巴士让学生从自己的宿舍去到上课的地方，但是因为UTMKL的campus不是很大所以校内就没有提供巴士服务，只需步行即可。
                  </v-expansion-panel-content>
                </v-expansion-panel>

                <v-expansion-panel>
                  <v-expansion-panel-header>
                    2. 每间宿舍都是一样的格局吗？
                  </v-expansion-panel-header>
                  <v-expansion-panel-content>
                    UTM的宿舍是双人房 (有床，橱，书桌，椅子，桌灯，风扇有)。
                    不过UTM JB宿舍的厕所是大家共用的，而UTM
                    KL的是两间房间共用一个厕所，所以厕所需要自行清洗。
                  </v-expansion-panel-content>
                </v-expansion-panel>

                <v-expansion-panel>
                  <v-expansion-panel-header>
                    3. 宿舍有wifi吗？要还钱的吗？
                  </v-expansion-panel-header>
                  <v-expansion-panel-content>
                    UTM 有提供公用wifi，是免费的。但是需要在同学入学之后使用UTM
                    id 和password
                    才能使用。但是每天凌晨4点都会重置，需要每天重新login！有些网站也会受到限制，不可以浏览。
                  </v-expansion-panel-content>
                </v-expansion-panel>

                <v-expansion-panel>
                  <v-expansion-panel-header>
                    4. 宿舍里面可以煮东西吗？煮水的话嘞？
                  </v-expansion-panel-header>
                  <v-expansion-panel-content>
                    UTM
                    的宿舍是不允许煮食物的；同学可以在宿舍煮水，需自行准备煮水的电器并在报到时缴付电器的费用。
                  </v-expansion-panel-content>
                </v-expansion-panel>

                <v-expansion-panel>
                  <v-expansion-panel-header>
                    5. Foundation 的一年里可以不住宿舍吗？
                  </v-expansion-panel-header>
                  <v-expansion-panel-content>
                    可以的，可以选择住在宿舍或者在Campus外居住；如果在campus外居住，须在每个sem返回学校填写表格。
                  </v-expansion-panel-content>
                </v-expansion-panel>

                <v-expansion-panel>
                  <v-expansion-panel-header>
                    6. 在Johor 的Foundation
                    的宿舍，是有一个人居住的吗？是多少钱？
                  </v-expansion-panel-header>
                  <v-expansion-panel-content>
                    校方提供给学生的宿舍都是默认为双人房的，如果要选择单人房的话需要向校方申请并且提供合理的原因，再看校方是否通过哦。Foundation学生双人房的宿舍费总共是RM3000
                  </v-expansion-panel-content>
                </v-expansion-panel>

                <v-expansion-panel>
                  <v-expansion-panel-header>
                    7. 房间有hair dryer吗？
                  </v-expansion-panel-header>
                  <v-expansion-panel-content>
                    没有哦，需要自备。
                  </v-expansion-panel-content>
                </v-expansion-panel>

                <v-expansion-panel>
                  <v-expansion-panel-header>
                    8.
                    第一天去宿舍时候要自备垃圾桶，扫把，畚斗那些的打扫用具吗？
                  </v-expansion-panel-header>
                  <v-expansion-panel-content>
                    可以自备，也可以选择当天在学校才去附近购买。
                  </v-expansion-panel-content>
                </v-expansion-panel>

                <v-expansion-panel>
                  <v-expansion-panel-header>
                    9. 可以带小冰箱吗？
                  </v-expansion-panel-header>
                  <v-expansion-panel-content>
                    不建议这么做哦，因为被抓到的话是会被罚款甚至有可能被充公的。
                  </v-expansion-panel-content>
                </v-expansion-panel>

                <v-expansion-panel>
                  <v-expansion-panel-header>
                    10.
                    房间里面有问题比如灯坏掉还是插座不能用的话，是要自己修吗？
                  </v-expansion-panel-header>
                  <v-expansion-panel-content>
                    可以通知宿舍的管理员，他们会尽量在几天内来帮你处理好。
                  </v-expansion-panel-content>
                </v-expansion-panel>

                <v-expansion-panel>
                  <v-expansion-panel-header>
                    11. 我在房间里面的话可以穿短裤吗？
                  </v-expansion-panel-header>
                  <v-expansion-panel-content>
                    可以，只要不是在宿舍外面的范围走动。
                  </v-expansion-panel-content>
                </v-expansion-panel>

                <v-expansion-panel>
                  <v-expansion-panel-header>
                    12. 可以选择自己要住的宿舍吗？
                  </v-expansion-panel-header>
                  <v-expansion-panel-content>
                    这点没办法哦，因为都是校方的安排。但是学校会尽量安排学生住在靠近上课地点的宿舍。而Foundation的同学呢，根据utmspace的网站，男生是住在K10，然而女生将会住在K9宿舍。
                  </v-expansion-panel-content>
                </v-expansion-panel>

                <v-expansion-panel>
                  <v-expansion-panel-header>
                    13. 我可以申请跟朋友一起住吗？
                  </v-expansion-panel-header>
                  <v-expansion-panel-content>
                    根据学校的规定，是无法选择自己的宿舍和室友的。但是通常校方都会尽量安排学生和同科系同种族的一起住哦，所以别担心。
                  </v-expansion-panel-content>
                </v-expansion-panel>

                <v-expansion-panel>
                  <v-expansion-panel-header>
                    14. UTM Skudai的男女宿舍长怎样的？
                  </v-expansion-panel-header>
                  <v-expansion-panel-content>
                    <p class="text-decoration-underline text-center title">
                      <v-icon color="blue">mdi-gender-male</v-icon>
                      男宿舍
                    </p>
                    <v-row>
                      <v-col
                        ><v-img
                          src="../assets/k10a.jpg"
