                      ></v-col>
                    </v-row>

                    <p class="text-decoration-underline text-center title mt-8">
                      <v-icon color="pink">mdi-gender-female</v-icon>
                      女宿舍
                    </p>
                    <v-row>
                      <v-col md="6"
