          <v-row class="mt-16" id="environment_faq">
            <v-col>
              <p class="headline text-center">
                <v-icon
                  color="red"
                  class="animate__animated animate__fadeInLeft animate__infinite"
                  >mdi-hand-pointing-right</v-icon
                >
                校内&校外环境与生活问题
                <v-icon
                  color="red"
                  class="animate__animated animate__fadeInRight animate__infinite"
                  >mdi-hand-pointing-left</v-icon
                >
              </p>

              <v-expansion-panels>
                <v-expansion-panel>
                  <v-expansion-panel-header>
                    1. Utm里面有什么特别的设施？
                  </v-expansion-panel-header>
                  <v-expansion-panel-content>
                    校内有泳池，体育场、篮球场等等还有各式各样的地方等待你自己去探索~
                    <br />
                    PS：迫不及待的同学可以先看看工大的校园介绍短片哟！
                    <v-row>
                      <v-col>
                        <iframe
                          class="hidden-sm-and-down"
                          width="560"
                          height="315"
                          src="https://www.youtube-nocookie.com/embed/JT7LedfgX1Q"
                          title="YouTube video player"
                          frameborder="0"
                          allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
                          allowfullscreen
                        ></iframe>
                        <iframe
                          class="hidden-md-and-up"
                          src="https://www.youtube-nocookie.com/embed/JT7LedfgX1Q"
                          title="YouTube video player"
                          frameborder="0"
                          allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
                          allowfullscreen
                        ></iframe>
                      </v-col>
                    </v-row>

                    <v-row>
                      <v-col>
                        UTM KL宿舍Kolej Siswa Jaya(KSJ)简介： <br />
                        <a
                          class="red--text"
                          href="https://drive.google.com/file/d/1lRHwuUzH3DL5nM46-fm1mXayOpRmld67/view?usp=sharing"
                          target="_blank"
                          >Kolej Kediaman Siswa Jaya.pdf</a
                        >
                      </v-col>
                    </v-row>
                  </v-expansion-panel-content>
                </v-expansion-panel>

                <v-expansion-panel>
                  <v-expansion-panel-header>
                    2. 学校附近有market吗？</v-expansion-panel-header
                  >
                  <v-expansion-panel-content
                    >UTMJB 附近有
                    market让同学购买日常用品。此外校内也有杂货店方便学生购买必需品。
                  </v-expansion-panel-content>
                </v-expansion-panel>

                <v-expansion-panel>
                  <v-expansion-panel-header>
                    3. 通常生活费是多少？</v-expansion-panel-header
                  >
                  <v-expansion-panel-content
                    >生活费是偏向于个人的，校内的食物选择琳琅满目，价钱平均为RM5一餐。另外还需要考虑买书本、文具、复印教材的费用、和其他生活娱乐之类的。一个月的生活费建议准备至少RM500。
                  </v-expansion-panel-content>
                </v-expansion-panel>

                <v-expansion-panel>
                  <v-expansion-panel-header>
                    4.
                    那么你们是去哪里洗衣服的？洗了晒在哪里？</v-expansion-panel-header
                  >
                  <v-expansion-panel-content>
                    除了可以选择手洗，大部分宿舍内也有提供洗衣机。校内也有提供自助自已的dobi，dobi内也有烘干机。宿舍外面通常都会有一个晒衣服的地方，如果不放心的话也可以选择晒在自己房间，但是需要自备架子或绳子。</v-expansion-panel-content
                  >
                </v-expansion-panel>

                <v-expansion-panel>
                  <v-expansion-panel-header
                    >5. 校内有华人餐吗？校外的话会难找吗？
                  </v-expansion-panel-header>
                  <v-expansion-panel-content>
                    校内有华人素食。同时也可以选择乘坐Grab或巴士到校外附近的Taman
                    U。或者是用Food
                    Panda选择叫Delivery，和校外的华人餐厅外卖。</v-expansion-panel-content
                  >
                </v-expansion-panel>

                <v-expansion-panel>
                  <v-expansion-panel-header>
                    6. 宿舍里面有饮水机吗？</v-expansion-panel-header
                  >
                  <v-expansion-panel-content
                    >UTMJB的话，在步行可到的范围内，宿舍和上课的地方附近都会有饮水机，但是需注意有些饮水机需要收零钱而有些则是免费的。而UTMKL的宿舍内，双层的楼层都会有一台饮水机。
                  </v-expansion-panel-content>
                </v-expansion-panel>

                <v-expansion-panel>
                  <v-expansion-panel-header>
                    7.
                    宿舍的房间有电风扇是吗？然后tilam不用带对吗？</v-expansion-panel-header
                  >
                  <v-expansion-panel-content
                    >宿舍提供的风扇是安装在天花板的吊扇哦。而tilam则是不需要带的因为宿舍是有提供床褥的，如果同学觉得有需要的话也是可以带的。
                  </v-expansion-panel-content>
                </v-expansion-panel>

                <v-expansion-panel>
                  <v-expansion-panel-header>
                    8. UTM JB的公交设施如何？</v-expansion-panel-header
                  >
                  <v-expansion-panel-content
                    >B 呢有Bus Muafakat
                    Johor正在运行，负责载送居民到达目的地。车费呢是一人一年RM10，必须申请专属的BMJ
                    Kad，在2021年，BMJ为了推动到柔佛旅游计划，手持大马卡的马来西亚公民可以以身份证上车，无需任何费用。BMJ也有一辆巴士进入UTM
                    JB区域载送学生出校等等（目前MCO期间，无法确定是否照常入内）
                    <br /><br />
                    除了BMJ之外，JB也有Causewaylink以及众多巴士公司在市区里面载送，车费不详，需要根据你的目的地付费，并且只可以使用现金付费。
                    <br />
                    <a
                      href="https://drive.google.com/drive/folders/1P6zVO0e_CXz0S9OBWoQ8smv0eKRv7KDr?usp=sharing"
                      target="_blank"
                      class="red--text"
                      >点击我查看路线</a
                    >
                    <br /><br />
                    <p class="red--text font-weight-bold">
                      *如果要查看任何关于P211的时间以及路线的话，请下载LUGO或者使用Google
                      Map
                    </p>
                  </v-expansion-panel-content>
                </v-expansion-panel>

                <v-expansion-panel>
                  <v-expansion-panel-header
                    >9.
                    如果我家住靠近学校或者我是住外面的话，需要怎样搭公共交通到学校呢？</v-expansion-panel-header
                  >
                  <v-expansion-panel-content>
                    UTM KL路线： <br /><br />
                    路线一 <br />
                    搭LRT去到
                    <span class="yellow">Dato Keramat</span> 然后在那里等个
                    <span class="yellow">220号</span>
                    的巴士并搭上，220号巴士有一站会<span class="yellow"
                      >停在UTM KL大门对面的巴士站</span
                    >。 <br /><br />
                    路线二 <br />
                    若不太想等巴士，也可以搭 LRT 去到
                    <span class="yellow">Damai</span>
                    然后走个大约15分钟就能到达UTM KL的Gurney Mall（校内的Mall）
                  </v-expansion-panel-content>
                </v-expansion-panel>
              </v-expansion-panels>
            </v-col>
          </v-row>

          <v-row class="mt-16" id="degree_faq">
            <v-col>
              <p class="headline text-center">
                <v-icon
                  color="red"
                  class="animate__animated animate__fadeInLeft animate__infinite"
                  >mdi-hand-pointing-right</v-icon
                >
                往后升学
                <v-icon
                  color="red"
                  class="animate__animated animate__fadeInRight animate__infinite"
                  >mdi-hand-pointing-left</v-icon
                >
              </p>

              <v-expansion-panels>
                <v-expansion-panel>
                  <v-expansion-panel-header>
                    1. 读了utm的Foundation过后可以去别的大学读degree吗？
                  </v-expansion-panel-header>
                  <v-expansion-panel-content>
                    可以通过UPU
                    申请去到其他的大专（UMK,UMP,UTeM,UNIMAP,UPNM,USM）就读学士课程。但是如果在UPU
                    获得了offer，<span class="font-weight-bold"
                      >UTM 内部提供foundation 的offer 会自动取消。<span
                        class="yellow"
                        >(更新于9月15号，这个事情不属实，所以也代表UTM招生部欺骗学生。建议学生注意以及小心关注，若有兴趣申请其他大学，需要担心该有的风险哦）</span
                      ></span
                    >
                  </v-expansion-panel-content>
                </v-expansion-panel>

                <v-expansion-panel>
                  <v-expansion-panel-header>
                    2. 我们读完了，想要报读自己喜欢的科系，进main
                    stream是通过upu，还是还有一个管道直接进main stream？
                  </v-expansion-panel-header>
                  <v-expansion-panel-content>
                    即将读完foundation后校方会提供一个表格给学生填写在utm
                    degree的选择。而根据以往的经验，如果foundation的成绩在3.20以上的话utm会直接offer学生在main
                    stream
                    degree升学，但是最终决定还是需要取决于校方。如果没有3.20也不用担心，因为utm
                    foundation的成绩也可以用来去upu申请再进到main
                    stream或是一些大专的degree课程，具体的课程信息可以点击下边的链接来查看。<br />
                    <a
                      class="red--text"
                      href="https://drive.google.com/file/d/1g2JIzOp49o4udAc0B9Kr0-YqbQkHlpu8/view"
                      target="_blank"
                      >可以通过UPU并且使用UTM
                      Foundation的成绩的其他大学的课程.pdf</a
                    >
                    <br />
                    <span class="font-weight-bold red--text"
                      >勿听信谗言，问老师最实在！</span
                    >
                  </v-expansion-panel-content>
                </v-expansion-panel>

                <v-expansion-panel>
                  <v-expansion-panel-header>
                    3. 通过UTM
                    Foundation的特别管道的话，有什么科系是我可以申请的呢？
                  </v-expansion-panel-header>
                  <v-expansion-panel-content>
                    大部分科系都可以申请，Nuclear Engineering、Quantity
                    Surveying、一些education科目，更加详细的建议参阅以下的pdf
                    file。 <br />
                    <a
                      class="red--text"
                      href="https://drive.google.com/file/d/11w0VUzLMzyD5-KYOEU3ZR_ZkfUvQOrDP/view?usp=sharing"
                      target="_blank"
                      >UTM Degree Application List 2021 - UTM Foundation.pdf</a
                    >
                    <br />
                    <a
                      class="red--text"
                      href="https://drive.google.com/file/d/1Njh6Q0Pp7H1yXTXiO_ouPE1lzZ-9tWLP/view?usp=sharing"
                      target="_blank"
                      >UTM Degree Application List 2022 - UTM Foundation.pdf</a
                    >
                  </v-expansion-panel-content>
                </v-expansion-panel>

                <v-expansion-panel>
                  <v-expansion-panel-header>
                    4. 我读了UTM Foundation，是不是只可以申请IDP？
                  </v-expansion-panel-header>
                  <v-expansion-panel-content>
                    完全不正确，在你读到Foundation一半的时候，UTM的招生处会通知并且给你们讲座。到时候，UTM会有特别通道给你们申请进入UTM。（大概3月左右）
                  </v-expansion-panel-content>
                </v-expansion-panel>

                <v-expansion-panel>
                  <v-expansion-panel-header>
                    5. 我想问下，比如说asasi
                    utm的学生跟其他管道进的相比会有在选择科系上的优势，那请问utm的asasi是不是也有这种优势呢？
                  </v-expansion-panel-header>
                  <v-expansion-panel-content>
                    在utm的asasi课程将要结束时，utmspace会先开放utm的degree课程给asasi的学生申请，学生可选择其中两个。而可选择的科系校方会根据学生自身读者的课程去进行过滤。utm也有说明会优先考量asasi的学生。
                    <br />
                    录取过程如下： <br />
                    UTM Foundation → （剩余的位置）→ UPU → Matriculation → STPM
                    → Diploma Other Universities → Foundation IPTS <br />
                    在2020届的degree招生简章说明会的时候，UTM Intake
                    Department也表示大部分UTM Foundation的学生都会获得UTM
                    Mainstream的录取通知书。她很少看到UTM
                    Foundation的学生没有获得UTM Mainstream的录取通知书。<span
                      class="yellow font-weight-bold"
                      >(更新于9月15号，这个事情不属实，所以也代表UTM招生部欺骗学生。建议学生注意以及小心关注，若有兴趣申请其他大学，需要担心该有的风险哦）</span
                    >
                    <br />
                    <span class="font-weight-bold">但是注意</span>，从2021
                    年开始，毕业的asasi
                    生已经开始无法通过这个内部申请的方式来获得UTM
                    Degree一些课程，不能申请的课程会因年而异（2021
                    是architecture,2022 年是quantity surveying)
                    。如果那年没有提供Architecture/Quantity
                    Surveying但又想报读的同学可以考虑通过UPU申请。此外，如果不知道那年有没有提供想要读的科系又担心没有提供的话可以考虑报读UTM
                    Diploma。UTM Diploma有提供quantity
                    surveying、architecture等等的科系（<a
                      class="red--text"
                      href="https://space.utm.my/wp-content/uploads/2021/09/ebrochure_dipFT.pdf"
                      target="_blank"
                      >UTM Diploma科系列表&门槛</a
                    >），而且也能在degree申请回同样的科系。 <br />
                    <span class="font-weight-bold text-decoration-underline"
                      >UTM Diploma</span
                    >
                    <br />
                    读utm diploma的话进utm degree的时候可以credit
                    transfer一年，transfer后degree可以少读一年。 <br />
                    <span class="font-weight-bold red--text"
                      >***UTM Diploma in
                      Architecture需要面试以及参加考试，方可获取录取通知书</span
                    >
                  </v-expansion-panel-content>
                </v-expansion-panel>

                <v-expansion-panel>
                  <v-expansion-panel-header>
                    6. 请问下读完foundation后如果我以International Degree
                    Program (IDP) 进入就读degree和通过main
                    stream的进入会有什么不一样呢？
                  </v-expansion-panel-header>
                  <v-expansion-panel-content>
                    两者的差别是在于费用以及门槛哦IDP门槛偏低一些，平时上课是一起的。
                  </v-expansion-panel-content>
                </v-expansion-panel>

                <v-expansion-panel>
                  <v-expansion-panel-header>
                    7. IDP 和mainstream的degree有什么差别？
                  </v-expansion-panel-header>
                  <v-expansion-panel-content>
                    IDP
                    degree是utm自主录取学生，而mainstream的degree则是通过upu也就是国家高等教育局来录取学生的。
                  </v-expansion-panel-content>
                </v-expansion-panel>

                <v-expansion-panel>
                  <v-expansion-panel-header>
                    8. 我在Foundation的时候是拿Life
                    Science的，请问我在申请mainstream的时候是不是只可以到有Biology的mainstream科目？
                  </v-expansion-panel-header>
                  <v-expansion-panel-content>
                    这种说法是错的！Life Science的科目仍然符合到UTM
                    Mainstream课程的录取指标。举例来说，Electronic或者Electrical或者Mechatronic的录取资格是接受使用biology（详细见以下的文件）
                  </v-expansion-panel-content>
                </v-expansion-panel>

                <v-expansion-panel>
                  <v-expansion-panel-header>
                    9. 我看到入学门槛表里面有写着需要MUET Band
                    2或以上才可以拿到offer，请问我们如何考MUET？
                  </v-expansion-panel-header>
                  <v-expansion-panel-content>
                    UTM
                    Space会帮忙学生在degree之前考MUET，费用呢大约是RM350-RM400之间，这笔费用里面包括UTM
                    Space给予的MUET的补习班。如果你已经自己考过MUET的话，并且你的MUET成绩还在有效期的话，你可以直接凭着该成绩申请degree
                    Mainstream课程。 <br />
                    <span class="font-weight-bold"
                      >建议学生们自己报考MUET，因为自己报考MUET可以比较便宜而且可以自己选择考场，不需要支付昂贵的考试费。（只是不会给予补习班，需要自习）</span
                    >
                    <br />
                    <span class="font-weight-bold"
                      >Normal Fee: MUET : RM101 / MOD: RM250</span
                    >
                    <br />
                    <span class="font-weight-bold"
                      >MUET :Register online and make payment during
                      registration.</span
                    >
                    <br />
                    <span class="font-weight-bold"
                      >MoD: Register online and make payment after
                      registration.</span
                    >
                    <v-img src="../assets/muet.png" max-width="1453"></v-img>
                  </v-expansion-panel-content>
                </v-expansion-panel>
              </v-expansion-panels>
            </v-col>
          </v-row>

          <v-row class="mt-16" id="upu_faq">
            <v-col>
              <p class="headline text-center">
                <v-icon
                  color="red"
                  class="animate__animated animate__fadeInLeft animate__infinite"
                  >mdi-hand-pointing-right</v-icon
                >
                UPU申请问题
                <v-icon
                  color="red"
                  class="animate__animated animate__fadeInRight animate__infinite"
                  >mdi-hand-pointing-left</v-icon
                >
              </p>

              <v-expansion-panels>
                <v-expansion-panel>
                  <v-expansion-panel-header>
                    1. 若我想要使用UTM
                    Foundation成绩来申请UPU的话，我需要在Kategori跟Lepasan填写什么？我需要在BSN买哪里一个类型的pin？
                  </v-expansion-panel-header>
                  <v-expansion-panel-content>
                    Lepasan: Foundation IPTS <br />
                    Jenis Kelayakan: Kelayakan Setaraf <br />
                    Type of BSN Pin: Kelayakan Setaraf
                  </v-expansion-panel-content>
                </v-expansion-panel>

                <v-expansion-panel>
                  <v-expansion-panel-header>
                    2. 为什么不是Lepasan Asasi ? 我们不是Program Asasi吗？
                  </v-expansion-panel-header>
                  <v-expansion-panel-content>
                    在UPU里面Program Asasi只专属于UKM,UM,UiTM的Program
                    Asasi的课程，因为他们的成绩是自动与教育部的系统连接，UTM的Program
                    Asasi属于UTM自己开办的课程，未纳入教育部的课程内，所以Kategori是Kelayakan
                    Setaraf. (如果你选择Program
                    Asasi的话，系统也不会给你process的，因为在教育部的系统内没有你的资料）。
                    <br />
                    在Bahagian Akademik的部分，请在Ruang STPM的Keterangan
                    Diploma/Setaraf填写如下：
                    <v-img
                      src="../assets/foundation_enddate.jpg"
                      max-width="751"
                    ></v-img>
                    <span class="font-weight-bold"
                      >Tarikh Tamat
                      Pengajian必须询问老师，因为每年的日期都不同！</span
                    >
                  </v-expansion-panel-content>
                </v-expansion-panel>

                <v-expansion-panel>
                  <v-expansion-panel-header>
                    3. 在Bahagian Dokumen的，我要怎样upload我的文件？
                  </v-expansion-panel-header>
                  <v-expansion-panel-content>
                    你必须根据他所需要的来upload给他，如果有标记 *
                    号的话，但是你没有的，你可以跳过，因为已经向UPU官员确认，该
                    * 号只是摆美的。 <br />
                    <span class="font-weight-bold"
                      >Dokumen Penting！！！！</span
                    >
                    <v-simple-table>
                      <tbody>
                        <tr>
                          <th>Salinan Sijil SPM</th>
                          <td>
                            如果你已经拿到了，可以scan了,
                            upload给他们，若还没，请上传SPM Slip
                          </td>
                        </tr>
                        <tr>
                          <th>Salinan Transkrip Semasa</th>
                          <td>
                            这个是你Current CPGA的slip，你可以到
                            <a
                              class="red--text"
                              href="https://apps.foundation.utmspace.edu.my/student/login?returnUrl=/student/"
                              target="_blank"
                              >SMP</a
                            >（Sistem Maklumat Pelajar），print出来或者save as
                            PDF
                          </td>
                        </tr>
                        <tr>
                          <th>Salinan Pengesahan Tamat</th>
                          <td>
                            这个必须向学校索取，前提是你已经完成了asasi课程，如果还没请放空
                          </td>
                        </tr>
                        <tr>
                          <th>Salinan Sijil MQA</th>
                          <td>
                            请下载这个document，然后upload。请点击
                            <a
                              class="red--text"
                              href="https://www2.mqa.gov.my/mqr/english/epapar_printAA.cfm?IdAkrKP=17343"
                              target="_blank"
                              >下载</a
                            >
                            来download.
                          </td>
                        </tr>
                        <tr>
                          <th>Salinan Sijil MUET</th>
                          <td>
                            如果你已经考取了，请上传你的MUET Certificate,
                            如果还没请放空。
                          </td>
                        </tr>
                      </tbody>
                    </v-simple-table>
                  </v-expansion-panel-content>
                </v-expansion-panel>

                <v-expansion-panel>
                  <v-expansion-panel-header>
                    4. 有部份的dokumen我没有upload，我放空，会不会影响到我的UPU
                    Application？
                  </v-expansion-panel-header>
                  <v-expansion-panel-content>
                    不会影响到的，别担心，UPU会在8月的时候重新开放系统，让申请者upload剩下的资料，以确保不会影响到application哦！
                  </v-expansion-panel-content>
                </v-expansion-panel>
              </v-expansion-panels>
            </v-col>
          </v-row>
        </v-col>
      </v-row>
    </v-container>
  </div>
